<script>export let size = '20';
export let color = 'currentColor';
export let variation = 'outline';
export let ariaLabel = 'chevron down';
export let role = 'img';
let viewBox;
let svgpath;
let svgoutline = `<path stroke-linecap="round" stroke-linejoin="round" d="M19 9l-7 7-7-7" stroke="${color}"></path>`;
let svgsolid = `<path clip-rule="evenodd" fill-rule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" fill="${color}"></path>`;
$: switch (variation) {
    case 'outline':
        svgpath = svgoutline;
        viewBox = '0 0 20 20';
        break;
    case 'solid':
        svgpath = svgsolid;
        viewBox = '0 0 20 20';
        break;
    default:
        svgpath = svgoutline;
        viewBox = '0 0 20 20';
}
</script>

<svg
  xmlns="http://www.w3.org/2000/svg"
  width={size}
  height={size}
  class={$$props.class}
  {...$$restProps}
  aria-label={ariaLabel}
  fill="none"
  {viewBox}
  {role}
  stroke-width="2"
  on:click>
  {@html svgpath}
</svg>

